<template>
  <div class="list-container">
    <div class="list-header">
      <div class="list-heading">
			<div>{{ heading }} ({{ array.length }}) </div>
			<button 
				class="btn btn-outline-dark" 
				@click="$emit('expand')"
				style="margin-left: 0.5rem; "
			><i class="icon-unfold-more"></i></button>
			<button 
				class="btn btn-outline-dark" 
				@click="$emit('collapse')"
			><i class="icon-unfold-less"></i></button>
			<button 
				class="btn btn-outline-dark" 
				@click="$emit('toggle-vis')"
			><i class="icon-expand-less"></i></button>
		  </div>
      <slot name="header"></slot>
    </div>
    
    <transition-group name="list" tag="ul" class="table-list">
      <slot></slot>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'TableArray',
  props: ['heading', 'array'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>



