	background-color: #393f46 !important;
	color: $dark-warm;
	display: flex;
	flex-direction: column;
	font-family: "Source Sans Pro", "Helvetica Neue LT Std", Helvetica, Arial, sans-serif;
	height: 100vh;
	width: 100vw;
}

#app {
	display: grid;
	grid-auto-flow: column;
	grid-template: 
		min-content min-content 1fr
		/ 1fr 19rem;
	height: 100%;
	overflow: hidden;
}

